___CSS_LOADER_EXPORT___.push([module.id, `:root {
	--1: #f7f7f7;
	--2: #eeeeee;
	--3: #393e46;
	--4: #929aab;
	--5: #38598b;
	--6: #a2a8d3;
}

* {
	box-sizing: border-box;
	font-family: "Schibsted Grotesk", sans-serif;
	margin: 0;
	padding: 0;
	border: 0;
}

body {
	display: grid;
	height: 100vh;
	grid-template-rows: 1fr 12fr 1fr;
	background-color: var(--3);
}

/* ---------------header--------------- */

header {
	display: flex;
}

#title {
	color: var(--2);
	letter-spacing: 3px;
	padding: 5px;
	padding-left: 20px;
	padding-right: 20px;
	border: 2px solid #0a66c2;
	border-radius: 20px;
	margin: 10px;
	text-shadow: 5px 5px 10px rgba(135, 135, 135, 0.4);
}

/* ------------------------------------ */

/* ---------------nav--------------- */

nav {
	display: flex;
	flex-direction: column;
	padding: 10px;
	gap: 5px;
	border-right: 1px solid var(--3);
}

a {
	list-style: none;
	text-decoration: none;
}

a:visited {
	color: black;
}

a:hover {
	cursor: pointer;
	text-decoration: underline;
}

.projectButton {
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: 5px;
}

.deleteButton {
	height: 20px;
	opacity: 0;
	font-size: 0.6rem;
	font-weight: 600;
	border: 1px solid black;
	color: rgb(0, 0, 0);
	padding: 2px;
	border-radius: 3px;
	background-color: rgb(233, 100, 100);
}

.deleteButton:hover {
	cursor: pointer;
	border: 1px solid red;
	opacity: 0.5;
	transition: 0.3s;
}

/* #addProjectButton {
	scale: 0.6;
	font-size: 0.7rem;
	width: fit-content;
	padding: 5px;
	padding-left: 10px;
	padding-right: 10px;
	border-radius: 5px;
	border: 1px solid transparent;
} */

#addProjectButton:hover,
#editProject:hover,
#addTask:hover,
button.projectInput:hover {
	cursor: pointer;
	border: 1px solid var(--3);
}

.visible {
	/* display: block !important; */
	visibility: visible !important;
	opacity: 1 !important;
}

.displayNone {
	display: none;
}

#projectInput,
#taskInput {
	display: flex;
	flex-direction: column;
	visibility: hidden;
	opacity: 0;
	transition: opacity 0.3s ease-in;
	padding: 20px;
	gap: 25px;
	background-color: rgb(159, 156, 156);
	border-radius: 5px;
	box-shadow: 0px 0px 12px 2000px rgba(0, 0, 0, 0.8);
	position: fixed;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 350px;
	border: 1px solid black;
}

.radio-inputs {
	position: relative;
	align-self: center;
	display: flex;
	flex-wrap: wrap;
	border-radius: 0.5rem;
	background-color: #eee;
	box-sizing: border-box;
	box-shadow: 0 0 0px 1px rgba(0, 0, 0, 0.06);
	width: 300px;
	font-size: 14px;
}

.radio-inputs .radio {
	flex: 1 1 auto;
	text-align: center;
}

.radio-inputs .radio input {
	display: none;
}

.radio-inputs .radio .name {
	display: flex;
	cursor: pointer;
	align-items: center;
	justify-content: center;
	border-radius: 0.5rem;
	border: none;
	padding: 0.5rem 0;
	color: rgba(51, 65, 85, 1);
	transition: all 0.15s ease-in-out;
}

.radio-inputs .radio input:checked + .name {
	background-color: #fff;
	font-weight: 600;
}

input,
textarea {
	resize: none;
	padding: 5px;
	outline: none;
	border-radius: 4px;
	box-shadow: 0px 0px 1px 1px rgba(0, 0, 0, 0.5);
}

input:focus {
	outline: 1px solid rgba(0, 0, 0, 0.3);
}

.together {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.togetherButtons {
	display: flex;
	justify-content: space-evenly;
	margin-top: 15px;
}

button.projectInput,
button.taskInput {
	width: 30%;
	border-radius: 5px;
	padding: 3px;
	border: 1px solid transparent;
}

#submitProject,
#submitTask {
	background-color: rgba(0, 128, 0, 0.571);
	border: 1px solid transparent;
}

#cancelSubmitProject,
#cancelSubmitTask {
	background-color: rgba(158, 17, 17, 0.626);
	border: 1px solid transparent;
}

#submitTask:hover,
#cancelSubmitTask:hover,
#submitProject:hover,
#cancelSubmitProject:hover {
	border: 1px solid black;
	scale: 1.05;
	transition: 0.3s;
	cursor: pointer;
}

label {
	font-size: 0.7rem;
	padding: 2px;
}

.join {
	align-items: center;
	display: flex;
	justify-content: space-between;
	padding: 5px;
	padding-bottom: 0;
	/* border-bottom: 1px solid var(--3); */
	height: 50px;
}

/* ---------------main--------------- */

main {
	display: grid;
	grid-template-columns: minmax(350px, 20vw) 3fr;
	background-color: var(--2);
}

section {
	display: flex;
	flex-direction: column;
	padding: 10px;
}

.intro {
	display: flex;
	flex-direction: column;
}

.editTogether {
	display: flex;
	justify-content: space-between;
}

#projectDesc {
	margin-top: 20px;
	padding: 5px;
}

#editProject {
	visibility: hidden;
}

#addTask {
	visibility: hidden;
	font-size: 1.25rem;
	min-width: 50px;
}

#taskContainer {
	padding: 20px;
	border-radius: 5px;
	display: flex;
	flex-direction: column;
	gap: 5px;
}

.taskCard {
	font-size: 0.7rem;
	padding: 10px;
	border: 1px solid rgba(0, 0, 0, 0.205);
	border-radius: 5px;
	display: flex;
	justify-content: space-between;
	gap: 30px;
}

.delete {
	display: none;
	font-size: 0.6rem;
	font-weight: 600;
	border: 1px solid black;
	color: rgb(0, 0, 0);
	padding: 2px;
	border-radius: 3px;
	background-color: rgb(233, 100, 100);
}

.delete:hover {
	background-color: rgb(228, 191, 122);
	scale: 1.025;
	border: 1px solid black;
	transition: 0.2s;
	cursor: pointer;
}

.desc {
	display: none;
	max-width: 50ch;
	padding: 10px;
	font-size: 0.6rem;
}

.name {
	font-weight: bold;
}

.Low,
.Medium,
.Urgent {
	display: flex;
	justify-content: center;
	font-size: 0.6rem;
	padding: 5px;
	border: 1px solid rgba(0, 0, 0, 0.25);
	border-radius: 15px;
}

.Low {
	background-color: rgba(152, 238, 152, 0.694);
}

.Medium {
	background-color: rgba(243, 203, 131, 0.731);
}

.Urgent {
	background-color: rgb(232, 96, 96);
}

.showButton {
	width: 21.5px;
	background-color: transparent;
}

.showButton:hover {
	cursor: pointer;
	color: white;
	scale: 1.05;
}

.taskJoin {
	display: flex;
	flex-direction: column;
	justify-content: center;
}

.taskComplete {
	display: flex;
	align-items: center;
	gap: 5px;
}

input[type="checkbox"] {
	box-shadow: none;
}

input[type="checkbox"]:focus {
	outline: none;
}

.taskNameAndButton,
.taskNameAndButtonX {
	display: flex;
	align-items: center;
}

.flex {
	/* border-bottom: 1px solid var(--3); */
	display: flex;
	align-items: center;
	gap: 30px;
}

button {
	background-color: var(--6);
}

/* ---- */

@media only screen and (max-width: 755px) {
	main {
		display: block;
	}

	nav {
		gap: 0;
		border-radius: 5px;
		border: 1px solid;
		margin: 10px;
	}

	#currentProject {
		font-size: 1em;
	}

	#projectDesc {
		margin: 0;
	}

	#taskContainer {
		padding: 0;
	}

	.taskCard {
		margin: 0;
	}

	.taskNameAndButtonX {
		flex-direction: column-reverse;
		align-items: baseline;
	}
	.desc {
		padding-left: 0;
	}
}

.button-18 {
	min-width: 100px;
	height: 30px;
	align-items: center;
	background-color: #0a66c2;
	border: 1px solid transparent;
	border-radius: 100px;
	color: #ffffff;
	cursor: pointer;
	display: inline-flex;
	font-size: 12px;
	font-weight: 600;
	justify-content: center;
	line-height: 20px;
	max-width: 480px;
	overflow: hidden;
	padding: 0px;
	padding-left: 15px;
	padding-right: 15px;
	text-align: center;
	touch-action: manipulation;
	transition: background-color 0.167s cubic-bezier(0.4, 0, 0.2, 1) 0s,
		box-shadow 0.167s cubic-bezier(0.4, 0, 0.2, 1) 0s,
		color 0.167s cubic-bezier(0.4, 0, 0.2, 1) 0s;
	user-select: none;
	-webkit-user-select: none;
	vertical-align: middle;
}

.button-18:hover,
.button-18:focus {
	background-color: #16437e;
	color: #ffffff;
}

.button-18:active {
	background: #09223b;
	color: rgb(255, 255, 255, 0.7);
}

.button-18:disabled {
	cursor: not-allowed;
	background: rgba(0, 0, 0, 0.08);
	color: rgba(0, 0, 0, 0.3);
}
